
























































export default {
	data() {
		return {
			userInfo: {
				nickname: "未设置",
				mobile: "未绑定",
				head_img: "../../../static/head-no-pic.png",
				vip_name: "普通会员",
				is_validate: 0,
				realname: "",
				telphone:'000-00000000',
			},



		}
	},
	onLoad() {},
	onShow() {
		this.getUserInfoData();
	},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	onBackPress() {
		uni.switchTab({
			url: '/pages/tabBar/user/user'
		})
	},
	onNavigationBarButtonTap(e) {},
	methods: {
		getUserInfoData() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			try {
				this.$http.post(this.websiteUrl + '/api/api/getUserInfoData', {
					openid,
					sessionKey
				}, (res) => {
					if (res.data.code == 1) {
						this.userInfo = res.data.result.userInfo;
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		},
		goDetailPage(url) {
			uni.navigateTo({
				url: url
			})
		},
		makePhoneCall: function () {
			uni.makePhoneCall({
				phoneNumber: this.userInfo.telphone,
				success: () => {
					console.log("成功拨打电话")
				}
			})
		},
		logoutUser: function(){
			this.$store.commit('logout');
			uni.reLaunch({
				url: '/pages/tabBar/index/index'
			})
		},
		checkVersion: function(){



































		}
	}
}
